<template>
  <div class="home">
    <div class="header">
      <p>hi~ 同学</p>
      <el-menu text-color="#000" active-text-color="#C00000" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router>
        <el-menu-item index="/students/practiceList">练习列表</el-menu-item>
        <el-menu-item index="/students/taskList">作业列表</el-menu-item>
      </el-menu>
      <div>
        <el-button @click="rush" class="start_btn" type="danger">我抢</el-button>
        <el-button @click="logout">退出</el-button>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import { studentsRush } from '../../../api/rush.js'
export default {
  data () {
    return {
      activeIndex: this.$route.path
    }
  },

  created () {

  },

  methods: {
    logout () {
      this.$router.push('/login')
    },
    async rush () {
      const { data: res } = await studentsRush()
      if (res.code === 400002) return this.$message.error('您以抢答成功，无需再次抢答')
      if (res.code === 400003) return this.$message.error('抢答已结束或未开始')
      this.$message.success('您已参与抢答，请勿重复抢答')
    }
  }
}
</script>

<style scoped lang='less'>
.home {
  .header {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    align-items: center;
    background-color:#e3e6ea;
    .start_btn {
      background-color: #C00000;
    }
    .el-menu {
      background-color: transparent;
      .el-menu-item:hover, .el-menu-item:focus {
        background-color: transparent;
      }
    }
  }
}
</style>
